.form_button {
    width: 10rem;
    height: 4rem;
    font-size: large;
    transition-duration: 0.2s;
    border: none;
    margin-bottom: 5%;
}

.form_button:hover {
    transition-duration: 0.2s;
    box-shadow: 0px 0px 10px 0.2px;
}

.main_box {
    /* background-image: url("./bottomBrownWood.jpg"); */
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: fit-content;
    margin: auto;
    margin-top: 5%;
    margin-bottom: 2%;
    border-radius: 2rem;
    transition-duration: 0.3s;
}

.main_box:hover {
    box-shadow: 0px 0px 34px 3px black;
}

h1 {
    font-size: 4rem;
    color: beige;
}

.input_section {
    border: none;
    border-radius: 20px;
    padding-left: 1rem;
}