.list_border {
    border: none;
    background-color: #0e17c6;
    margin: auto;
    margin-bottom: 2%;
    padding: 0% 2% 0% 2%;
    margin-top: 2%;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: fit-content;
    transition-duration: 0.4s;
    color: rgb(255, 255, 255);
    border-radius: 2rem;
}

.list_border:hover {
    background-color: rgb(40, 255, 244);
    box-shadow: 0px 0px 15px 2px rgb(40, 255, 244);
    transition-duration: 0.4s;
}

.input_section {
    width: 30rem;
    height: 2rem;
    font-size: larger;
    margin: 2rem;
}